.Title {
  font-size: 56px;
  font-family: "Amiri";
  letter-spacing: 0px;
  color: black;
  text-decoration: none;
}

.Subtitle {
  font-size: 28px;
  font-weight: 400;
  letter-spacing: 0px;
  color: lavender;
  font-family: "Helvetica Neue";

  margin-top: -15px;
  padding-bottom: 50px;
}

.Paragraph {
  font-family: 'Amiri';
  text-align: justify;
  text-justify: inter-word;

  padding-top: 30px;
  font-size: 26px;
  max-width: 800px;
  line-height: 1.2;
}

/* For Mobile */
@media screen and (max-width: 540px) {
  .Title {
    letter-spacing: -2px;
  }

  .Subtitle {
    margin-top: -25px;
    padding-bottom: 30px;
  }

  .Paragraph {
    font-size: 20px;
    max-width: 350px;
    line-height: 1.3;
  }
}

